import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
export default {
	data() {
		return {
			id:'',
			list:[]
		}
	},
	onLoad(e) {
		this.id = e.id
		this.getData()
	},
	methods: {
		// 编辑
		edit(item){
			uni.navigateTo({
				url:'../../pagesTwo/MyProductSend/MyProductSend?type='+item.type+'&id='+item.id
			})
		},
		// 去发布
		Issue(){
			uni.navigateTo({
				url:'../../pagesTwo/MyProductSend/MyProductSend?type=1000'
			})
		},
		// 详情
		myProduct(item){
			uni.navigateTo({
				url:'../../pagesTwo/MyProduct/MyProduct?id='+item.id + "&type="+item.type
			})
		},
		// 删除
		del(item){
			Dialog.confirm({
			  title: '提示',
			  message: '您确定删除吗？'
			
			}).then(() => {
				this.$api.del_company_services_products({
					token: uni.getStorageSync("token"),
					user_id: uni.getStorageSync("userId"),
					id:item.id,
					type:item.type
				}).then(res=>{
					uni.showToast({
					    title: res.data.msg,
					    duration: 2000,
						icon:'none'
					});
					if(res.data.code=='200'){
						this.getData()
					}
				})
				
			}).catch(() => {
			  // on cancel
			});
			
		},
		getData(){
			
			this.$api.company_services_products({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				company_id:this.id
			}).then(res=>{
				console.log(res)
				this.list = res.data.data
			})
		}
	}
}
